<template>
  <div id="hello">
    <h2 id="notice" :style="{ opacity: noticeOpacity }"></h2>
    <ul>
      <div style="text-align:left;"><span>日常话术</span></div>
      <li class="box" v-for="(item, index) in items" :key="index" @click="copyImageOrTextToClipboard(item)"
        @mouseover="showPreview(item.img)" @mouseleave="hidePreview" :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" v-if="showPreviewFlag" :style="{ backgroundImage: `url(${previewImg})` }"
          @click.stop="copyImageOrTextToClipboard(item)" @mouseenter="hidePreview"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div style="text-align:left;"><span>iOS话术</span></div>
      <li class="box" v-for="(item, index) in ios" :key="index" @click="copyImageOrTextToClipboard(item)"
        @mouseover="showPreview(item.img)" @mouseleave="hidePreview" :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" v-if="showPreviewFlag" :style="{ backgroundImage: `url(${previewImg})` }"
          @click.stop="copyImageOrTextToClipboard(item)" @mouseenter="hidePreview"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div style="text-align:left;"><span>Windows话术</span></div>
      <li class="box" v-for="(item, index) in windows" :key="index" @click="copyImageOrTextToClipboard(item)"
        @mouseover="showPreview(item.img)" @mouseleave="hidePreview" :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" v-if="showPreviewFlag" :style="{ backgroundImage: `url(${previewImg})` }"
          @click.stop="copyImageOrTextToClipboard(item)" @mouseenter="hidePreview"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div style="text-align:left;"><span>macOS话术</span></div>
      <li class="box" v-for="(item, index) in mac" :key="index" @click="copyImageOrTextToClipboard(item)"
        @mouseover="showPreview(item.img)" @mouseleave="hidePreview" :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" v-if="showPreviewFlag" :style="{ backgroundImage: `url(${previewImg})` }"
          @click.stop="copyImageOrTextToClipboard(item)" @mouseenter="hidePreview"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div style="text-align:left;"><span>Android话术</span></div>
      <li class="box" v-for="(item, index) in android" :key="index" @click="copyImageOrTextToClipboard(item)"
        @mouseover="showPreview(item.img)" @mouseleave="hidePreview" :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" v-if="showPreviewFlag" :style="{ backgroundImage: `url(${previewImg})` }"
          @click.stop="copyImageOrTextToClipboard(item)" @mouseenter="hidePreview"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>
    <div style="clear: both;"></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'HelloWorld',
  data() {
    return {
      showPreviewFlag: false,
      previewImg: '',
      noticeOpacity: 0
    };
  },
  computed: {
    ...mapGetters(['items', 'ios', 'windows', 'mac', 'android'])
  },
  methods: {
    async copyImageOrTextToClipboard(item) {
      try {
        // 检查浏览器是否支持 Clipboard API
        if (!navigator.clipboard) {
          console.error('Clipboard API is not supported.');
          return;
        }

        // 复制文本内容
        if (item.content === '') {
          if (item.img === '') {
            console.error('无可复制内容');
            return;
          }
          const imgBlob = await fetch(item.img).then(response => response.blob());
          const clipboardItemInput = new ClipboardItem({ 'image/png': imgBlob });
          await navigator.clipboard.write([clipboardItemInput]);
          console.log('图片已成功复制到剪贴板');
        } else {
          await navigator.clipboard.writeText(item.content);
          console.log('文字内容已成功复制到剪贴板');
        }

        // 显示复制成功提示
        this.noticeOpacity = 1;
        document.getElementById('notice').innerText = '复制成功';

        // 一段时间后隐藏复制成功提示
        setTimeout(() => {
          this.noticeOpacity = 0;
          document.getElementById('notice').innerText = '';
        }, 1000);

      // 使用备用方法来复制文本
      if (!navigator.clipboard) {
        console.error('Clipboard API is not supported.');
        document.execCommand('copy');
        console.log('文本已成功复制到剪贴板');
        return;
      }

      if (item.content === '') {
        if (item.img === '') {
          console.error('无可复制内容');
          return;
        }
        const imgBlob = await fetch(item.img).then(response => response.blob());
        const clipboardItemInput = new ClipboardItem({ 'image/png': imgBlob });
        await navigator.clipboard.write([clipboardItemInput]);
        console.log('图片已成功复制到剪贴板');
      } else {
        navigator.clipboard.writeText(item.content)
          .then(() => {
            console.log('文字内容已成功复制到剪贴板');
          })
          .catch(err => {
            console.error('复制失败: ', err);
          });
      }
      this.noticeOpacity = 1;
      document.getElementById('notice').innerText = '复制成功';

      setTimeout(() => {
        this.noticeOpacity = 0;
        document.getElementById('notice').innerText = '';
      }, 1000);
    } catch(err) {
      console.error('复制到剪贴板失败：', err);
    }
  },
  showPreview(img) {
    this.showPreviewFlag = true;
    this.previewImg = img;
  },
  hidePreview() {
    this.showPreviewFlag = false;
    this.previewImg = '';
  }
}
}
</script>

